<template>
  <div class="panel panel-order-course">
    <van-tabs v-model="active" :sticky="true" color="#36A5DC" title-active-color="#333333" title-inactive-color="#999999" :line-width="28" :line-height="2">
      <van-tab title="开通连续续费" v-if="withhold.length">
        <div class="course-info">
          <img :src="merchandise.picture" alt="course logo">
          <div class="col-flex">
            <h4 class="ellipsis">{{merchandise.name}}</h4>
            <p>时长：{{merchandise.duration}}个月</p>
            <b data-unit="￥">{{merchandise.amount / 100}}</b>
          </div>
        </div>
        <dl ref="dl">
          <dt>选择续费计划</dt>
          <dd v-for="(item, i) in withhold" :key="i" :class="{active: selectIndex === i}" @click.stop.prevent="selectPlan(i)">
            <div class="content">
              <h5><span>首付{{item.month}}个月课程</span><span class="hint mid-flex" v-if="item.downPaymentReduction">立减 {{item.downPaymentReduction / 100}} 元</span></h5>
              <p><span>次月起连续续费 {{item.remainderPeriod}} 期：</span><b data-unit="￥">{{(item.monthlyMortgage / 100).toFixed(2)}} / 月</b> <span class="rate" v-if="item.monthReduction">{{(item.monthReduction / 100).toFixed(2)}}</span></p>
            </div>
            <div class="price col-flex">
              <b data-unit="￥">{{(item.downPayment / 100).toFixed(2)}}</b>
              <b data-unit="￥" class="ori-price" v-if="item.downPaymentReduction">{{((item.downPayment + item.downPaymentReduction)/ 100).toFixed(2) }}</b>
            </div>
          </dd>
        </dl>
      </van-tab>
      <van-tab title="开通分期付款" v-if="instalments.downPayment">
        <div class="course-info">
          <img :src="merchandise.picture" alt="">
          <div class="col-flex">
            <h4 class="ellipsis">{{merchandise.name}}</h4>
            <p>时长：{{merchandise.duration}}个月</p>
            <b data-unit="￥">{{merchandise.amount / 100}}</b>
          </div>
        </div>
        <van-cell title="首付金额">
          <span class="payment" data-unit="￥">{{(instalments.downPayment / 100).toFixed(2)}}</span>
        </van-cell>
        <div class="staging">
          <h6>按月分期</h6>
          <p class="flex-wrap"><span class="mid-flex" :class="{active: selectPeriod === period}" v-for="period in instalments.selectPeriod.split(',')" :key="period" @click.stop="selectPeriod = period">{{period}}个月</span></p>
        </div>
        <van-cell title="月供" class="van-hairline--bottom">
          <span class="payment" data-unit="￥" v-show="monthlyMortgage">{{monthlyMortgage}}</span>
        </van-cell>
      </van-tab>
    </van-tabs>
    <van-cell title="续费银行卡" is-link to="/bankcard-info">
      <span v-if="!bankcard.bankcardNo">未绑定</span>
      <div class="bankcard-info flex-wrap" v-else>
        <img :src="bankcard.logo" alt="">
        <span>{{bankcard.bankcardName}}</span>
        <span>{{bankcard.bankcardNo.slice(-4)}}</span>
      </div>
    </van-cell>
    <div class="statement">
      <h5>课程续费声明</h5>
      <p>1. 开通续费成功后,支付首付期数为上课权限生效期数,后续正常续费方可开启剩余上课权限,直到续费支付完所有款项后开启完整学习权限。</p>
      <p>2. 开通续费成功后,系统会根据订单续费计划,定期自动续费。 </p>
      <p>3. 自动续费可随时在我的-设置中关闭,关闭后续费服务停止。</p>
    </div>
    <div class="footer">
      <div class="agreement">
        <checkbox :init-checked="checked" v-on:update:checkbox="newV => checked = newV"></checkbox>
        <span>同意</span>
        <!-- <a :href="`./index.html#/protocol-2?openId=${openId}`"></a> -->
        <!-- <a :href="`./index.html#/protocol-3?openId=${openId}`">《连续续费协议》</a> -->
        <router-link to="/instalmentContract">《协议合同套件》</router-link>
        <router-link to="/instalmentContract">《连续续费协议》</router-link>
      </div>
      <van-button :disabled="disabled" type="primary" @click.stop.prevent="sureFunc">确认开通</van-button>
    </div>
  </div>
</template>

<script>
import checkbox from '@/components/checkbox.vue';
import { queryCourseData } from '@/api/index.js';
import { banks } from '@/assets/js/fields.js';

export default {
  name: 'order-course',
  props: ['id'],
  components: {
    checkbox,
  },
  data() {
    return {
      merchandise: {
        amount: 0,
      },
      withhold: [],
      instalments: {
        selectPeriod: '',
      },
      monthlyMortgage: 0,
      active: 0,
      selectIndex: -1,
      selectPeriod: -1,
      months: [1, 2, 3],
      checked: true,
      openId: '',
    };
  },
  computed: {
    bankcard() {
      return this.$store.state.bankcard;
    },
    isStaging() {
      if (this.withhold.length === 0) {
        return true;
      }
      if (!this.instalments.downPayment) {
        return false;
      }
      return this.active === 1;
    },
    disabled() {
      if (this.isStaging) {
        return !this.checked || this.selectPeriod === -1;
      }
      return !this.checked || this.selectIndex === -1;
    },
  },
  watch: {
    selectPeriod(newV) {
      if (newV > -1) {
        this.monthlyMortgage = (
          (this.merchandise.amount - this.instalments.downPayment)
          / (newV * 100)
        ).toFixed(2);
      }
    },
  },
  created() {
    this.courseId = this.$route.params.id;
    this.queryData();
  },
  methods: {
    selectPlan(i) {
      this.selectIndex = i;
      this.$refs.dl.scrollIntoView({
        behavior: 'smooth',
      });
    },
    sureFunc() {
      if (!this.bankcard.bankcardCode) {
        this.$toast('请先绑定银行卡');
        return;
      }
      this.$router.push({
        path: `/comfirmOrder/${this.courseId}/${
          !this.isStaging ? 500101 : 500102
        }/${
          !this.isStaging
            ? this.withhold[this.selectIndex].id
            : this.instalments.id
        }/${this.selectPeriod}`,
      });
    },
    queryData() {
      queryCourseData(this.courseId)
        .then((result) => {
          if (result) {
            this.merchandise = result.merchandise || {};
            this.withhold = result.withhold || [];
            this.instalments = result.instalments || {};
            if (result.hasDefBankCard) {
              const { code } = result.bankCard;
              const bankcardName = banks[code] || code;
              this.$store.commit('UPDATE_BANKCARD', {
                bankCardId: result.bankCard.id,
                bankcardCode: result.bankCard.code,
                logo: result.bankCard.logo,
                bankcardNo: result.bankCard.no,
                bankcardName,
              });
            }
          }
          console.log(result);
        })
        .catch(err => console.error(err));
    },
  },
};
</script>

