<template>
  <van-checkbox v-model="checked" @change="changeFunc">
    <img slot="icon" slot-scope="props" :src="props.checked ? icon.active : icon.normal" />
  </van-checkbox>
</template>

<script>
import normal from '../assets/images/unchecked-icon.png';
import active from '../assets/images/checked-icon.png';

export default {
  name: 'checkbox',
  props: {
    initChecked: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      icon: {
        normal,
        active,
      },
      checked: this.initChecked,
    };
  },
  methods: {
    changeFunc(value) {
      this.$emit('update:checkbox', value);
    },
  },
};
</script>

<style lang="scss" scoped>
.van-checkbox {
  border: 12px solid transparent;
  img {
    display: block;
    width: 16px;
    height: 16px;
  }
}
</style>
